.taskHoverBlock {
  position: absolute;
  z-index: 200;
  animation: fadeIn 0.3s ease-in-out;
}

.triggerContainer {
  display: flex;
  align-items: center;
}
.container {
  display: flex;
  width: max-content;
  flex-direction: column;
  padding: 10rem;
  border: 1px solid var(--border);
  background-color: var(--white);
  border-radius: 10px;
  box-shadow: 0px 3px 6px var(--shadow);
}

.element {
  padding: 10rem;
  border-radius: 6px;
  font-size: 14rem;
  font-weight: 400;
  text-align: left;
  transition: 0.2s;

  &:hover {
    background-color: var(--blue_secondary);
  }
  &:active {
    background-color: var(--blue_secondary);
  }
}


.dropDownOption {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 10rem;

  svg {
    margin-right: 10rem;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
