.wrap {
  padding: 10px;
  cursor: pointer;
}

.check {
  display: flex;
  width: 17rem;
  height: 17rem;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: 1px solid var(--gray);
  margin: 0 auto;
  background-color: transparent;
  border-radius: 4px;
}

.checked {
  border: 1px solid var(--blue);
  background-color: var(--blue);
}
